<template>
    <main id="todolist">
        <h1><span style="font-size:30px">Yapılacaklar Listesi</span>  <span>Vue.js ile kodlanan yapılacaklar listesi .</span></h1>
        <div class="items" >
            <ul>
                <li v-for="(icerik,index) in icerikler" :key="index" :class="{'done':icerik.yapildi}">
                    <span class="label">{{icerik.baslik}}</span>
                    <div class="actions">
                        <button class="btn-picto" type="button"  @click="durumdegistir(index)">

                            <i aria-hidden="true" class="material-icons">{{icerik.yapildi ? 'check_box' : 'check_box_outline_blank'}}</i>
                        </button>
                        <button class="btn-picto" type="button" aria-label="Delete" title="Delete" @click="iceriksil(index)">
                            <i aria-hidden="true" class="material-icons">delete</i>
                        </button>
                    </div>
                </li>
            </ul>
        </div>

        <p class="emptylist" v-if="icerikler.length<1">Yapılacaklar listen boş.</p>

        <form @submit.prevent="yeniicerikekle"> <!-- aslında butonoda @click ekleyerek yapabilrdik fakat kullanıcı enter'a basınca da tetiklensin diye forma submit ekledik .prevent ise form olduğu için bastıktan sonra yönlendirme yapmasın diye -->
            <label for="newitem">Yapılacaklar listene ekle</label>
            <input type="text" name="newitem" id="newitem" v-model="yeniicerik">
            <button type="submit">Ekle</button>
        </form>
    </main>
</template>


<script>

export default {
  data() {
    return {
      yeniicerik:'',
      icerikler:[],
     
    }
  },
  methods:{
   
    yeniicerikekle()
    {
       if(this.yeniicerik===''){return;}
      this.icerikler.push({
        baslik:this.yeniicerik,
        yapildi:false,
        
      })
    this.yeniicerik='';
    },
    iceriksil(index){
      this.icerikler.splice(index,1)
    },
    durumdegistir(index){
      
     
      this.icerikler[index].yapildi= !this.icerikler[index].yapildi
      
    
    },
   
   
  }
}
</script>

